const userLang = navigator.language || navigator.userLanguage;
export let lang;

if (userLang.startsWith("en")) {
  lang = 0; // English
} else if (userLang.startsWith("de")) {
  lang = 1; // German
} else if (userLang.startsWith("fr")) {
  lang = 2; // French
} else {
  lang = 0; // Default to English for any other language
}

